import React from "react";


class LayerB extends React.Component {
    state = {
        showDetails : false
    };

    render (){
        let rows = this.props.rows;
        return (

            rows.length === 0 ?
                <div> </div>
                :
                <div className={"layer-b"}>
                    { rows.map((elem, idx) => {
                        return (
                            <div className={"layer-c"} key={idx}>
                                <div> { elem.itemName } </div>
                                <div> { elem.count } </div>
                                <div> { (elem.unitPrice || "0").toLocaleString('en') } </div>
                            </div>

                        )

                    })}
                </div>)
    }
}


export default LayerB;