function setDevServerFavicon() {
    return new Promise( function( resolve, reject ) {
        var link = document.createElement( 'link' );
        link.rel  = 'shortcut icon';
        link.href = devFavicon;
        document.head.appendChild( link );
        link.onload = function() { resolve(); };
    } );
}
function LoadCSS( cssURL ) {
    return new Promise( function( resolve, reject ) {
        var link = document.createElement( 'link' );
        link.rel  = 'stylesheet';
        link.href = cssURL;
        link.integrity = 'sha384-UHRtZLI+pbxtHCWp1t77Bi1L4ZtiqrqD80Kn4Z8NTSRyMA2Fd33n5dQ8lWUE00s/';
        link.crossOrigin = 'anonymous';
        document.head.appendChild( link );
        link.onload = function() { resolve(); };
    } );
}

if(window.location.origin.includes('localhost')) setDevServerFavicon();
LoadCSS('https://use.fontawesome.com/releases/v5.6.3/css/all.css');

function App() {

    return (
        <div className="App">
            <Router>
                <Header/>
                {/*<Route exact path="/" component={Home} />*/}
                <div className={"wp"}>
                    <Route exact path="/aradschool" component={unit.Aradschool} />
                    <Route exact path="/monster" component={unit.Monster}/>
                    <Route exact path="/monster_dev" component={unit.Monster2}/>
                    <Route exact path="/cece" component={unit.Cece}/>
                    <Route exact path="/wd" component={Wd} />
                    <Route exact path="/emblem" component={unit.Emblem} />
                    <Route exact path="/rareAvatar" component={unit.RareAvatar} />
                    <Route exact path="/custom" component={unit.Custom} />
                    <Route exact path="/about" component={About} />

                    <div className={"honey-bee"}></div>
                </div>
            </Router>

            <Footer/>
        </div>
    );
}

export default App;
