import React from "react";
import { defaults, Line } from 'react-chartjs-2';
defaults.global.defaultFontFamily = 'Noto Sans KR';
defaults.global.defaultFontStyle = 'bold';
// defaults.global.defaultFontSize =  22;

class LineChart extends React.Component {

    getAverage = (array) => {
        if(!Array.isArray(array)) return 0;
        let sum = array.reduce((acc,cuv) => acc + cuv, 0);
        let avg = sum/array.length;
        return avg;
    };

    render() {
        let renderData = this.props.renderData || {};
        let renderArray = Object.keys(renderData);
        let query = this.props.query;
        let a = renderData[0] || {};
        let b = a[query] || [];
        let c = b.rows || [];
        let d = c[0] || {};

        let timeStampArray = renderArray.reduce((acc, cuv, idx) => {
            acc.push(new Date(renderData[cuv]._timeStamp).toLocaleString());
            return acc;
        }, []);

        let quantityArray = renderArray.reduce((acc, cuv, idx) => {
            let a = renderData[cuv] || {};
            let b = a[query] || [];
            let c = b.rows || [];
            let d = c.reduce((acc, cuv, idx) => {
                acc += cuv.count;
                return acc;
            },0);
            acc.push(d);
            return acc;
        },[]);

        let lowPriceArray = renderArray.reduce((acc, cuv, idx) => {
            let a = renderData[cuv] || {};
            let b = a[query] || [];
            let c = b.rows || [];
            let d = c[0] || {};
            let e = d.unitPrice || 0;
            acc.push(e);
            return acc;
        }, []);

        let averagePriceArray = renderArray.reduce((acc, cuv, idx) => {
            let a = renderData[cuv] || {};
            let b = a[query] || [];
            let c = b.rows || [];
            let d = c[0] || {};
            let e = d.averagePrice || 0;
            acc.push(e);
            return acc;
        }, []);

        let data = {
            labels : timeStampArray.reverse(),
            datasets : [
                {
                    label : 'ìµœì €ê°€',
                    borderColor : 'red',
                    borderWidth: 2,
                    pointRadius : 0,
                    backgroundColor : '#FFF0',
                    data : lowPriceArray.reverse(),
                    yAxisID : 'yAxes-1'
                },
                {
                    label : 'í‰ê· ê°€',
                    borderColor : '#337ab7',
                    borderWidth: 2,
                    pointRadius : 0,
                    backgroundColor : '#FFF0',
                    borderDash: [5, 5],
                    data : averagePriceArray.reverse(),
                    yAxisID : 'yAxes-1'
                },
                {
                    label : 'ë¬¼ëŸ‰',
                    borderColor : 'black',
                    borderWidth: 1,
                    pointRadius : 1,
                    data : quantityArray.reverse(),
                    yAxisID : 'yAxes-2'
                }
            ]
        };

        let options = {
            legend : {
            },
            scales : {
                yAxes : [
                    {
                        id : 'yAxes-1',
                        type :'linear',
                        display : true,
                        position : 'left',
                        ticks : {
                            maxTicksLimit : 10
                        }
                    },
                    {
                        id : 'yAxes-2',
                        type:'linear',
                        display: true,
                        position: 'right',
                        gridLines : {
                            display : false
                        },
                        ticks : {
                        }
                    }
                ]
            }
        };

        return (
            <Line
                data={ data }
                options={ options }
            />
        )
    }
}

export default LineChart;