import React from 'react';
import { Link } from "react-router-dom";
import logo from '../images/logo.png';
import '../styles/Header.css';
let origin = window.origin.toString();

class Header extends React.Component {

    state = {

    };

    componentDidMount () {
        let pathname = window.location.pathname;
        let elem = document.getElementsByClassName(pathname.replace('/',''))[0];
        if(elem !== undefined) elem.click();
    }

    toggleClass = (e) => {
        //e.preventDefault();
        let otherElems = document.querySelectorAll('.header__elem1__link');
        otherElems.forEach( elem => elem.classList.remove('--selected'));
        let elem = e.currentTarget.classList;
        elem.add('--selected');
    };

    cleanConsole = () => {
        console.clear();
    };

    render () {

        let simbol;
        if(origin.includes('osh')) simbol = 'a';
        else if (origin.includes('211')) simbol = 'ğŸ´';
        else if (origin.includes('local')) simbol = 'ğŸ¦„';

        return(
            <div className={"header"}>
                <div className={"header__elem1"}>
                    <div className={"header__elem1__logo"}> <img src={logo} width={35} height={35}/></div>
                    {/*<Link to="/aradschool" className={"header__elem1__link quick"} onClick={this.toggleClass}> ì™•ë¦½ì•„ë¼ë“œ </Link>*/}
                    <Link to="/monster" className={"header__elem1__link quick monster"} onClick={this.toggleClass}> ëª¬ìŠ¤í„° </Link>
                    <div className={"nav-vertical-bar"}>  </div>
                    {/*<Link to="/Wd" className={"header__elem1__link quick Wd"} onClick={this.toggleClass}> ê²€ìƒ‰ </Link>*/}
                    <Link to="/rareAvatar" className={"header__elem1__link quick rareAvatar"} onClick={this.toggleClass}> ë ˆì•• </Link>
                    <Link to="/emblem" className={"header__elem1__link quick emblem"} onClick={this.toggleClass}> ì— ë¸”ë ˜ </Link>
                    <Link to="/custom" className={"header__elem1__link quick custom"} onClick={this.toggleClass}> ì»¤ìŠ¤í…€ </Link>
                    {/*<Link to="/about" className={"header__elem1__link quick about"} onClick={this.toggleClass}> ì •ë³´ </Link>*/}

                </div>

                <div className={"header__elem2"}>
                    <div onClick={this.cleanConsole}> { simbol } </div>
                </div>
            </div>
            )
    }
}

export default Header;